import SelectTime from "../../plugin/components/SelectTime";
import InfoTable from "../../plugin/components/InfoTable";
import PageInfo from "../../plugin/script/common/PageInfo";
import BaseIframe from "../../plugin/script/common/BaseIframe";
import PermissionButton from "../../plugin/components/PermissionButton";
import UserHandler from "../../script/handlers/UserHandler";
import InputArea from "../../plugin/components/InputArea";
import InfoTableItem from "../../plugin/components/InfoTableItem";
export default {
  name: "lobby_flyer_config",
  extends: BaseIframe,
  components: {
    InfoTableItem,
    InputArea,
    SelectTime,
    InfoTable,
    PermissionButton
  },
  data() {
    /*校验手机号*/
    let checkPhone = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("手机号不能为空"));
      } else {
        if (/^[1][345789]\d{9}$/.test(value)) {
          callback();
        } else {
          return callback(new Error("请输入正确的手机号"));
        }
      }
    };
    return {
      dialogTitle: "",
      pagesize: 10,
      currentPage: 1,
      total: 0,
      formLabelWidth: "120px",
      dialogVisible: false,
      tableStyle: [
        { label: "渠道名称", prop: "channel", width: "" },
        { label: "KEY", prop: "channel_key", width: "" },
        { label: "图片一", prop: "pic_one_url", width: "" },
        { label: "图片二", prop: "pic_two_url", width: "" },
        { label: "图片三", prop: "pic_three_url", width: "" },
        { label: "操作时间", prop: "create_time", width: "" },
        { label: "操作", prop: "action", width: "" }
      ],
      tableData: [],
      pageInfo: new PageInfo(1, [5, 10, 15, 20], 6),
      dialogFormVisible: false,
      form: {
        channel_name: "",
        channel_key: "",
        word1: "",
        word1_type: "",
        word1_url: "",
        word1_jump_position: "",
        word2: "",
        word2_type: "",
        word2_url: "",
        word2_jump_position: "",
        word3: "",
        word3_type: "",
        word3_url: "",
        word3_jump_position: ""
      },
      channelOpts: [],
      wordTypeOpts: [],
      jumpPathOpts: [],
      form2: {
        name: ""
      },
      formLabelWidth: "100px",
      fileList: {
        imgList1: [],
        imgList2: [],
        imgList3: []
      }, //上传图片保存的图片信息列表
      imageUrl: {
        imgList1: "",
        imgList2: "",
        imgList3: ""
      },
      uploadHeaders: {
        Authorization: "application/json"
      }
    };
  },
  methods: {
    resetForm() {
      this.form = {
        channel_name: "",
        channel_key: "",
        word1: "",
        word1_type: "",
        word1_url: "",
        word1_jump_position: "",
        word2: "",
        word2_type: "",
        word2_url: "",
        word2_jump_position: "",
        word3: "",
        word3_type: "",
        word3_url: "",
        word3_jump_position: ""
      };
    },
    //获取渠道列表
    getChannelList() {
      let data = {
        type_id: 1,
        add_id: 4
      };
      this.$http.post("v1/backend/no_channel", data).then(res => {
        console.log(res);
        if (res.data.code === 1) {
          this.channelOpts = res.data.data;
        }
      });
    },
    //获取图片类型列表
    getPicTypeList() {
      this.$http
        .get("v1/backend/lobby/name_type", {
          params: {
            type_id: 7
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.wordTypeOpts = res.data.data;
          }
        });
    },
    //获取跳转路径列表
    getJumpPathList() {
      this.$http
        .get("v1/backend/lobby/name_type", {
          params: {
            type_id: 8
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.jumpPathOpts = res.data.data;
          }
        });
    },
    getAdvertiseConfList() {
      this.$http
        .get("v1/backend/lobby/flyer", {
          params: {
            page: this.currentPage,
            limit: this.pagesize
          }
        })
        .then(res => {
          console.log(res);
          this.tableData = res.data.data;
          this.total = res.data.total;
        });
    },
    openAddDialog() {
      this.dialogFormVisible = true;
      this.imageUrl = {
        imgList1: "",
        imgList2: "",
        imgList3: ""
      };
      this.getPicTypeList();
      this.resetForm();
      this.getChannelList();
    },
    addNewChannel() {
      if (!this.form.id) {
        let data = {
          name: this.form.channel_name,
          key: this.form.channel_key,
          name_one: "我是图片一",
          url_one: this.imageUrl.imgList1,
          type_one: Number(this.form.word1_type),
          jump_id_one: Number(this.form.word1_jump_position),
          name_two: "我是图片二",
          url_two: this.imageUrl.imgList2,
          type_two: Number(this.form.word2_type),
          jump_id_two: Number(this.form.word2_jump_position),
          name_three: "我是图片三",
          url_three: this.imageUrl.imgList3,
          type_three: Number(this.form.word3_type),
          jump_id_three: Number(this.form.word3_jump_position)
        };
        this.$http.post("v1/backend/lobby/flyer", data).then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.dialogFormVisible = false;
            this.getAdvertiseConfList();
          }
        });
      } else {
        let data = {
          banner_id: this.form.id,
          name: this.form.channel_name,
          key: this.form.channel_key,
          name_one: "我是图片一",
          url_one: this.imageUrl.imgList1,
          type_one: JSON.parse(this.form.word1_type),
          jump_id_one: JSON.parse(this.form.word1_jump_position),
          name_two: "我是图片二",
          url_two: this.imageUrl.imgList2,
          type_two: JSON.parse(this.form.word2_type),
          jump_id_two: JSON.parse(this.form.word2_jump_position),
          name_three: "我是图片三",
          url_three: this.imageUrl.imgList3,
          type_three: JSON.parse(this.form.word3_type),
          jump_id_three: JSON.parse(this.form.word3_jump_position)
        };
        this.$http.put("v1/backend/lobby/flyer", data).then(res => {
          console.log(res);
          if (res.data.code === 1) {
            this.dialogFormVisible = false;
            this.getAdvertiseConfList();
          }
        });
      }
    },
    handleEdit(row) {
      console.log(row);
      this.getPicTypeList();
      this.getJumpPathList();
      this.dialogFormVisible = true;
      this.dialogTitle = "更新大厅宣传页配置信息";
      this.form.id = row.id;
      this.form.channel_name = row.channel;
      this.form.channel_key = row.channel_key;
      this.form.word1_type = JSON.stringify(row.pic_one_type);
      this.form.word1_url = row.pic_one_url;
      this.form.word2_type = JSON.stringify(row.pic_two_type);
      this.form.word2_url = row.pic_two_url;
      this.form.word3_type = JSON.stringify(row.pic_three_type);
      this.form.word3_url = row.pic_three_url;
      this.form.word1_jump_position = JSON.stringify(row.jump_id_one);
      this.form.word2_jump_position = JSON.stringify(row.jump_id_two);
      this.form.word3_jump_position = JSON.stringify(row.jump_id_three);
      this.imageUrl.imgList1 = row.pic_one_url;
      this.imageUrl.imgList2 = row.pic_two_url;
      this.imageUrl.imgList3 = row.pic_three_url;
    },
    handleDelete(row) {
      console.log(row);
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning"
      })
        .then(() => {
          this.$http
            .delete("v1/backend/lobby/flyer", {
              params: {
                id: row.id
              }
            })
            .then(res => {
              if (res.data.code === 1) {
                this.getAdvertiseConfList();
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
              }
            });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除"
          });
        });
    },
    /**搜索*/
    search() {},
    handleSizeChange(val) {
      this.pagesize = val;
      this.getAdvertiseConfList();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.getAdvertiseConfList();
    },
    handleAvatarSuccess(res, file) {},
    handleChange(file, fileList, info) {
      this.fileList[info] = fileList;
    },
    beforeUpload(file) {},
    uploadFile(info) {
      let formData = new FormData();
      this.fileList[info].forEach(item => {
        formData.append("filename", item.raw);
        formData.append("types", 1);
      });
      this.$http.post("v1/backend/upload", formData).then(res => {
        if (res.data.code === 1) {
          this.imageUrl[info] = res.data.path;
        }
      });
    }
  },
  watch: {
    "form.word1_type": function(newVal, oldVal) {
      console.log(newVal);
      if (newVal === "4") {
        this.getJumpPathList();
      }
      if (newVal === "2" || ("3" && newVal !== "1")) {
        this.$nextTick(() => {
          this.form.word1_url = this.form.word1;
        });
      } else {
        this.$nextTick(() => {
          this.form.word1_url = "";
        });
      }
    }
  },
  mounted() {
    this.getAdvertiseConfList();
  }
};



